<template>
  <div class="over-view">
    <el-card style="margin-bottom: 10px">
      <div class="title">
        我的待办健康管理任务
      </div>
      <div class="card-container">
        <div class="card" @click="gotoDetail('follow')">
          <div class="card-title-bottom">健康随访</div>
          <div class="card-content">
            <div class="item">
              <div class="name">
                <i class="iconfont icon-jinri" />&nbsp;&nbsp;今日
              </div>
              <div v-if="statisticsData.list[0] > 0" class="count">
                {{ statisticsData.list[0] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
            <div class="item">
              <div class="name">
                <i class="iconfont icon-quanbu" />&nbsp;&nbsp;全部
              </div>
              <div v-if="statisticsData.list[1] > 0" class="count">
                {{ statisticsData.list[1] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
            <div class="item">
              <div class="name">
                <i class="iconfont icon-daihuifu" />&nbsp;&nbsp;待回复
              </div>
              <div v-if="statisticsData.list[2] > 0" class="count">
                {{ statisticsData.list[2] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
          </div>
        </div>
        <div class="card" @click="gotoDetail('guidance')">
          <div class="card-title-bottom">
            科普指导
          </div>
          <div class="card-content">
            <div class="item">
              <div class="name">
                <i class="iconfont icon-jinri" />&nbsp;&nbsp;今日
              </div>
              <div v-if="statisticsData.list2[0] > 0" class="count">
                {{ statisticsData.list2[0] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
            <div class="item">
              <div class="name">
                <i class="iconfont icon-quanbu" />&nbsp;&nbsp;全部
              </div>
              <div v-if="statisticsData.list2[1] > 0" class="count">
                {{ statisticsData.list2[1] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
            <div class="item">
              <div class="name">
                <i class="iconfont icon-daihuifu" />&nbsp;&nbsp;待回复
              </div>
              <div v-if="statisticsData.list2[2] > 0" class="count">
                {{ statisticsData.list2[2] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
          </div>
        </div>
        <div class="card" @click="gotoDetail('prevention')">
          <div class="card-title-bottom">
            预防提醒
          </div>
          <div class="card-content">
            <div class="item">
              <div class="name">
                <i class="iconfont icon-jinri" />&nbsp;&nbsp;今日
              </div>
              <div v-if="statisticsData.list3[0] > 0" class="count">
                {{ statisticsData.list3[0] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
            <div class="item">
              <div class="name">
                <i class="iconfont icon-quanbu" />&nbsp;&nbsp;全部
              </div>
              <div v-if="statisticsData.list3[1] > 0" class="count">
                {{ statisticsData.list3[1] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
            <div class="item">
              <div class="name">
                <i class="iconfont icon-daihuifu" />&nbsp;&nbsp;待回复
              </div>
              <div v-if="statisticsData.list3[2] > 0" class="count">
                {{ statisticsData.list3[2] }}
              </div>
              <div v-else class="zero-count">0</div>
            </div>
          </div>
        </div>
      </div>
    </el-card>
    <el-card>
      <el-tabs
        v-model="activeName"
        class="tab-container"
        style="padding-right: 20px"
      >
        <el-tab-pane label="批量发送" name="0" />
        <el-tab-pane name="1">
          <span slot="label">
            <div>
              <el-badge
                v-if="taskCountObj.todayListNum > 0"
                :value="taskCountObj.todayListNum"
                class="item"
              >
                已保存
              </el-badge>
              <span v-else>已保存</span>
            </div>
          </span>
        </el-tab-pane>
        <el-tab-pane name="2" label="全部已发送" />
        <el-tab-pane name="3" label="已取消" />
      </el-tabs>
      <div v-if="activeName === '0'">
        <div class="search-container">
          <search-conditions ref="searchConditions" :nurse="checkedNurse" />
        </div>
        <div class="batch-send" @click="batchSend">
          批量发送健康管理消息
        </div>
        <div class="table-title">
          <span v-for="(nurse, index) in replaceList" :key="index">
            <span
              class="cursor-pointer nurse-label"
              :class="{ 'highlight-nurse': checkedNurse === nurse._id }"
              @click="onHandleNurse(nurse)"
            >
              服务中的宝宝列表({{ nurse.name }})
              <svg-icon v-if="index !== 0" icon-class="replace" />
            </span>
          </span>
        </div>
        <el-table
          ref="babyTable"
          :data="babyList"
          :row-key="row => row.index"
          style="width: 100%"
          @selection-change="handleSelectionChange"
        >
          <el-table-column
            type="selection"
            :reserve-selection="true"
            width="55"
          />
          <el-table-column width="55" prop="index" label="序号" />
          <el-table-column align="center" header-align="center" label="宝宝">
            <template slot-scope="scope">
              <div style="text-align: center">
                <div>{{ scope.row.name }}</div>
                <div>{{ scope.row.babyId || scope.row.id }}</div>
              </div>
            </template>
          </el-table-column>
          <el-table-column align="center" header-align="center" label="用户">
            <template slot-scope="scope">
              <div>
                <div style="display: inline-block">
                  <el-avatar
                    :size="'small'"
                    :src="scope.row.user_info.avatar"
                    style="vertical-align: middle"
                  />
                  {{ scope.row.user_info && scope.row.user_info.nickname }}
                  <svg-icon v-if="scope.row.is_replace" icon-class="replace" />
                </div>
                <div>{{ scope.row.user_info && scope.row.user_info._id }}</div>
              </div>
            </template>
          </el-table-column>

          <el-table-column
            align="center"
            header-align="center"
            label="出生日期"
          >
            <template slot-scope="scope">
              {{ new Date(scope.row.birthday) | formatTime('y-m-d') }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            header-align="center"
            prop="age_of_month"
            label="月龄"
            width="120px"
          />
          <el-table-column align="center">
            <template slot="header">
              <el-tooltip
                class="item"
                effect="dark"
                content="接诊台宝宝资料处添加的备注"
                placement="top"
              >
                <div>宝宝备注<i class="el-icon-warning" /></div>
              </el-tooltip>
            </template>
            <template slot-scope="scope">
              <span :title="scope.row.remark">{{
                babyRemark(scope.row.remark)
              }}</span>
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            header-align="center"
            prop="serviceName"
            label="当前服务套餐"
          >
            <template slot-scope="scope">
              {{
                scope.row.fd_service_type_info &&
                  scope.row.fd_service_type_info.name
              }}
            </template>
          </el-table-column>
          <el-table-column
            align="center"
            header-align="center"
            prop="serviceTime"
            label="当前服务时间"
          >
            <template slot-scope="scope">
              {{
                new Date(
                  scope.row.fd_contract_info &&
                    scope.row.fd_contract_info.started_at
                ) | formatTime('y-m-d')
              }}
              至
              {{
                new Date(
                  scope.row.fd_contract_info &&
                    scope.row.fd_contract_info.ended_at
                ) | formatTime('y-m-d')
              }}
            </template>
          </el-table-column>
        </el-table>
        <div style="margin-top: 15px;text-align: center;">
          <el-pagination
            background
            :current-page.sync="currentPage"
            :page-sizes="[10, 50, 100]"
            :page-size="pageSize"
            layout="total, sizes, prev, pager, next, jumper"
            :total="total"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
          />
        </div>
      </div>
      <div v-else>
        <batch-tasklist
          ref="batchTask"
          :task-status="activeName"
          @refreshTab="onCancel"
        />
      </div>
    </el-card>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      :center="true"
      width="22%"
    >
      请选择批量发送健康管理消息的宝宝，您可以单击全选，勾选当前页面所有的宝宝
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogVisible = false"
          >确 定</el-button
        >
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapState } from 'vuex'
import { getStatistics, getAllTaskList } from '@/api/healthManagement'
import BatchTasklist from './components/BatchTasklist'
import SearchConditions from './components/SearchConditions'
import _ from 'lodash'
import { ellipsis } from '@/filters'

export default {
  name: 'Overview',
  components: {
    BatchTasklist,
    SearchConditions
  },
  data() {
    return {
      nurses: [{ name: this.$store.state.user.name, _id: 'self' }],
      isReplace: false,
      checkedNurse: 'self',
      dialogVisible: false,
      taskCountObj: {},
      searchForm: {
        babyName: '',
        serviceName: '',
        userName: '',
        userId: ''
      },
      multipleSelection: [],
      activeName: '0',
      product: '',
      statisticsData: {
        list: [],
        list2: [],
        list3: []
      }
    }
  },
  computed: {
    ...mapGetters(['babyList', 'serviceDic', 'getReplaceStatus']),
    ...mapState({
      total: state => state.health.babyCount
    }),
    currentPage: {
      get() {
        return this.$store.state.health.currentPage
      },
      set(val) {
        this.$store.commit('health/SET_CURRENTPAGE', val)
      }
    },
    pageSize: {
      get() {
        return this.$store.state.health.pageSize
      },
      set(val) {
        this.$store.commit('health/SET_PAGESIZE', val)
      }
    },
    replaceList() {
      const replace = _.get(this.getReplaceStatus, 'replace.replace_list')
      if (replace) {
        this.getStatistics()
      }
      return [
        {
          name: this.$store.state.user.name,
          _id: 'self'
        }
      ].concat(
        replace ? replace.map(replaceObj => replaceObj.replace_user) : []
      )
    }
  },
  watch: {
    activeName(val) {
      if (val !== '0') {
        // 待页面出现在dom中后再请求表格数据
        setTimeout(() => {
          this.$refs.batchTask.getAllTaskList()
        }, 200)
      }
    }
  },
  mounted() {
    const params = {
      show_replace: 0,
      service_item: 'health_manager'
    }

    this.getBabyList(params)
    this.getAllTaskList()
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      if (from.query.fromPage === 'saveList') {
        vm.activeName = '2'
      } else if (from.path.includes('batch-cancel')) {
        vm.activeName = '3'
      } else if (from.path.includes('batch-send')) {
        if (from.query.serviceType) {
          vm.activeName = '1'
        } else {
          vm.activeName = '0'
        }
      }
    })
  },
  methods: {
    ...mapActions({
      getBabyList: 'health/getBabyList'
    }),
    babyRemark(remark) {
      return remark ? ellipsis(remark, 10) : '宝宝备注'
    },
    onHandleNurse(nurse) {
      this.$refs.babyTable.clearSelection()
      this.$refs.searchConditions.clearSearchItem()
      this.pageSize = 10
      this.currentPage = 1
      this.checkedNurse = nurse._id
      if (nurse._id !== 'self') {
        const params = {
          bus_user: this.checkedNurse,
          show_replace: 0,
          service_item: 'health_manager'
        }
        this.getBabyList(params)
      } else {
        const params = {
          show_replace: 0,
          service_item: 'health_manager'
        }
        this.getBabyList(params)
      }
    },
    // 任务取消成功后
    onCancel() {
      this.getAllTaskList()
      this.activeName = '3'
    },
    getAllTaskList() {
      const params = {
        taskStatus: Number(this.activeName),
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }
      getAllTaskList(1, params).then(res => {
        if (res.code === 20000) {
          const data = res.data
          // 设置tab标签上的任务数量
          this.taskCountObj = {
            allListNum: data.allListNum,
            needToRelayNum: data.needToRelayNum,
            todayListNum: data.todayListNum
          }
        }
      })
    },
    // 获取首页统计数据
    getStatistics() {
      getStatistics().then(res => {
        if (res.code === 20000) {
          this.statisticsData = res.data
        }
      })
    },
    // 搜素
    search() {
      this.getBabyList(this.searchForm)
    },
    // 重置
    reset() {
      this.searchForm = {
        babyName: '',
        serviceName: '',
        userName: '',
        userId: ''
      }
      this.getBabyList()
    },
    batchSend() {
      const checkedBabyList = this.multipleSelection
      if (checkedBabyList.length === 0) {
        this.dialogVisible = true
      } else {
        this.$router.push({
          path: '/medical-service/batch-send',
          query: {
            babyList: checkedBabyList.join(','),
            isReplace: this.isReplace,
            // 代班情况下需要传递代班的健康管理师id
            hm: this.checkedNurse === 'self' ? '' : this.checkedNurse,
            batchStatus: 'new'
          }
        })
      }
    },
    handleSelectionChange(val) {
      if (val.length > 0) {
        this.isReplace = val[0].is_replace
      }
      this.multipleSelection = val.map(val => val._id)
    },
    gotoDetail(address) {
      this.$router.push(`main/${address}`)
    },
    handleSizeChange(val) {
      this.currentPage = 1
      this.pageSize = val
      // 页面变化后根据查询条件搜索
      this.$refs.searchConditions.search()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      // 页面变化后根据查询条件搜索
      this.$refs.searchConditions.search()
    }
  }
}
</script>
<style lang="scss">
@import '@/styles/healthManagement/overview.scss';

/* 覆盖样式改变表格行高 */
.over-view {
  .el-table__header tr,
  .el-table__header th {
    padding: 0;
    height: 50px;
  }
  .el-table__body tr,
  .el-table__body td {
    padding: 0;
    height: 50px;
  }
}
.tab-container {
  .el-badge__content.is-fixed {
    top: 10px;
  }
}
</style>
<style lang="scss" scoped>
.table-title {
  padding-left: 0;
}
.highlight-nurse {
  color: #0085ff;
  background-color: #fff;
}
.nurse-label {
  display: inline-block;
  padding: 0 30px;
  border-left: 1px solid #f5f7fa;
}
</style>
