<template>
  <div class="baby-search">
    <div class="baby-search__item">
      <div class="label">宝宝乳名</div>
      <div class="content">
        <el-input
          v-model="searchForm.babyName"
          placeholder="请输入宝宝乳名"
          size="mini"
        />
      </div>
    </div>
    <div class="baby-search__item">
      <div class="label">宝宝ID</div>
      <div class="content">
        <el-input
          v-model="searchForm.babyId"
          placeholder="请输入宝宝ID"
          size="mini"
        />
      </div>
    </div>

    <div class="baby-search__item">
      <div class="label">用户</div>
      <div class="content">
        <el-input
          v-model="searchForm.userName"
          placeholder="请输入用户昵称或备注"
          size="mini"
        />
      </div>
    </div>

    <div class="baby-search__item">
      <div class="label">用户ID</div>
      <div class="content">
        <el-input
          v-model="searchForm.userId"
          placeholder="请输入用户ID"
          size="mini"
        />
      </div>
    </div>

    <div class="baby-search__item">
      <div class="label">套餐名称</div>
      <div class="content">
        <service-package v-model="searchForm.serviceId"></service-package>
      </div>
    </div>

    <div class="baby-search__item search-button">
      <div>
        <el-button type="primary" size="mini" @click="startSearch"
          >搜索</el-button
        >
        <el-button size="mini" @click="reset">重置</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations } from 'vuex'
import ServicePackage from '@/components/SearchItem/ServicePackage'

export default {
  name: 'SearchConditions',
  props: {
    nurse: {
      type: String,
      default: ''
    }
  },
  components: {
    ServicePackage
  },
  data() {
    return {
      searchForm: {
        babyName: '',
        serviceId: '',
        userName: '',
        userId: '',
        babyId: ''
      }
    }
  },
  methods: {
    ...mapActions({
      getBabyList: 'health/getBabyList'
    }),
    ...mapMutations({
      setCurrentPage: 'health/SET_CURRENTPAGE',
      setPageSize: 'health/SET_PAGESIZE'
    }),
    // 搜索前页码组件复位
    startSearch() {
      this.setCurrentPage(1)
      this.setPageSize(10)
      this.search()
    },
    // 搜素
    search() {
      const params = {
        show_replace: 1,
        service_item: 'health_manager'
      }
      if (this.searchForm.babyName) {
        params.baby_name = this.searchForm.babyName
      }
      if (this.searchForm.userName) {
        params.user_name = this.searchForm.userName
      }
      if (this.searchForm.userId) {
        params.user_id = this.searchForm.userId
      }
      if (this.searchForm.serviceId) {
        params.fd_service_type = this.searchForm.serviceId
      }
      if (this.searchForm.babyId) {
        params.baby_id = this.searchForm.babyId
      }
      //  健康管理首页传入健康管理师ID时
      if (this.nurse) {
        params.show_replace = 0
        if (this.nurse !== 'self') {
          params.bus_user = this.nurse
        }
      }
      this.getBabyList(params)
    },
    clearSearchItem() {
      this.searchForm = {
        babyName: '',
        userName: '',
        userId: '',
        serviceId: '',
        babyId: ''
      }
    },
    // 重置
    reset() {
      this.clearSearchItem()
      this.setCurrentPage(1)
      this.setPageSize(10)
      const params = {
        show_replace: 1,
        service_item: 'health_manager'
      }
      //  健康管理首页传入健康管理师ID时
      if (this.nurse) {
        params.show_replace = 0
        if (this.nurse !== 'self') {
          params.bus_user = this.nurse
        }
      }
      this.getBabyList(params)
    }
  }
}
</script>

<style scoped lang="scss">
.baby-search {
  display: flex;
  align-items: center;
  justify-content: space-between;
  .search-button {
    justify-content: flex-end;
  }
  .baby-search__item {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
}
</style>
