<template>
  <div>
    <div class="table-title">
      批量发送消息列表
    </div>
    <el-table :data="tableData" style="width: 100%">
      <el-table-column prop="index" label="序号" width="80px" />
      <el-table-column
        :formatter="serviceType"
        prop="serviceType"
        label="服务类型"
      />
      <el-table-column label="标题" width="300px">
        <template slot-scope="scope">
          {{ scope.row.title }}
          <svg-icon v-if="scope.row.is_replace" icon-class="replace" />
        </template>
      </el-table-column>
      <el-table-column label="发送宝宝数量" width="120px">
        <template slot-scope="scope">
          {{ scope.row.babyList ? scope.row.babyList.length : 0 }}
        </template>
      </el-table-column>
      <el-table-column label="创建人">
        <template slot-scope="scope">
          {{ scope.row.creator ? scope.row.creator.name : '未知' }}
        </template>
      </el-table-column>
      <el-table-column prop="sender" label="发送人">
        <template slot-scope="scope">
          {{ scope.row.sender ? scope.row.sender.name : '未知' }}
        </template>
      </el-table-column>
      <el-table-column label="最近编辑时间" width="220px">
        <template slot-scope="scope">
          {{ new Date(scope.row.editTime) | formatTime() }}
        </template>
      </el-table-column>
      <el-table-column label="完成状态">
        <template slot-scope="scope">
          <div>
            <div>
              {{ scope.row.status | finishStatus }}
            </div>
            <div v-if="scope.row.delayDay" style="font-size: 12px;color: red;">
              延期{{ scope.row.delayDay }}天
            </div>
          </div>
        </template>
      </el-table-column>
      <el-table-column prop="address" label="操作"
        ><template slot-scope="scope">
          <div>
            <el-button
              type="text"
              size="small"
              @click="handleClick(scope.row)"
              >{{ switchHandleText(scope.row.status) }}</el-button
            >
            <span
              v-if="scope.row.status === 2"
              class="red-cancle"
              type="text"
              size="small"
              @click="cancelTask(scope.row._id)"
              >&nbsp;&nbsp;取消发送</span
            >
          </div>
        </template>
      </el-table-column>
    </el-table>
    <div style="margin-top: 10px;text-align: center;">
      <el-pagination
        background
        :current-page.sync="currentPage"
        :page-sizes="[10, 50, 100]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next"
        :total="total"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
    <el-dialog
      title="提示"
      :visible.sync="dialogVisible"
      :center="true"
      width="22%"
    >
      取消后，已编辑的内容无法再编辑，如果想要修改内容后发送，请单击列表操作列去发送，可以编辑上次保存的内容
      <span slot="footer" class="dialog-footer">
        <el-button type="primary" @click="confirmCancel">确定取消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getAllTaskList, cancelTask } from '@/api/healthManagement'
import { mapGetters } from 'vuex'

export default {
  name: 'TaskList',
  props: {
    taskStatus: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      dialogVisible: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      tableData: [],
      cancelTaskId: ''
    }
  },
  computed: {
    ...mapGetters(['babyList', 'selectBaby'])
  },
  methods: {
    confirmCancel() {
      cancelTask(this.cancelTaskId).then(res => {
        if (res.code === 20000) {
          this.dialogVisible = false
          this.$message.success('任务取消成功')
          this.getAllTaskList()
          this.$emit('refreshTab')
        }
      })
    },
    cancelTask(taskId) {
      this.dialogVisible = true
      this.cancelTaskId = taskId
    },
    serviceType(row) {
      if (row.serviceType === 1) {
        return '科普指导'
      } else if (row.serviceType === 2) {
        return '预防提醒'
      }
    },
    switchHandleText(status) {
      switch (status) {
        case 2:
          return '去发送'
        case 3:
          return '查看详情'
        case 5:
          return '查看详情'
      }
    },
    getAllTaskList() {
      const params = {
        taskStatus: Number(this.taskStatus),
        currentPage: this.currentPage,
        pageSize: this.pageSize
      }
      getAllTaskList(1, params).then(res => {
        if (res.code === 20000) {
          const data = res.data
          this.tableData = data.list
          this.total = data.count
        }
      })
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getAllTaskList()
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getAllTaskList()
    },
    handleClick(row) {
      const babyNum = row.babyList.length
      const taskId = row._id
      const babyList = row.babyList.join(',')
      if (row.status === 2) {
        this.$router.push({
          path: '/medical-service/batch-send',
          query: { babyList, taskId, serviceType: row.serviceType }
        })
      } else if (row.status === 3) {
        this.$router.push({
          path: '/medical-service/batch-detail',
          query: { babyNum, taskId, fromPage: 'saveList' }
        })
      } else if (row.status === 5) {
        this.$router.push({
          path: '/medical-service/batch-cancel',
          query: { babyNum, taskId }
        })
      }
    }
  }
}
</script>

<style scoped lang="scss">
.red-cancle {
  font-size: 12px;
  color: #f0717b;
  cursor: pointer;
}
.table-title {
  padding: 0 30px;
  width: 100%;
  height: 48px;
  line-height: 48px;
  color: #17233d;
  background-color: #f5f7fa;
}
</style>
